.station-details {
    display: grid;
    grid-template-columns: 2/-1;
    grid-template-rows: 1/-1;
    height: 100%;
    width: 100%;
    border: 0;
    padding-top: 17px;
    vertical-align: baseline;
    background-image: linear-gradient(rgb(26, 26, 172), #121212);
    // z-index: 2;
}

.top-container {
    display: flex;

    .img-container {
        width: rem(230px);
        height: rem(230px);
        box-shadow: rgba(0, 0, 0, 0.5) 0px 4px 60px 0px;
        margin-top: 69px;
        margin-left: 34px;
        object-fit: cover;
        aspect-ratio: 1 / 1;
    }

    .info-container {
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: flex-start;
        color: $txtselected;
        margin-left: 24px;
        margin-top: 100px;

        h2 {
            font-size: rem(12px);
        }

        h1 {
            margin-top: 18px;
            font-size: 6rem;
        }

        p {
            margin-top: 20px;
            font-size: rem(12px);
        }
    }
}

.details-controls {
    display: flex;

    .btn-play {
        display: flex;
        height: rem(55px);
        width: rem(55px);
        background-color: transparent;
        background-color: $bgcplay;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        margin-top: 38px;
        margin-left: 32px;

        svg {
            fill: black;
            font-size: 25px;
        }
    }

    .btn-like {
        background-color: transparent;
        margin-left: 34px;
        margin-top: 45px;
        font-size: 25px;

    }

    .btn-options {
        background-color: transparent;
        margin-top: 45px;
        margin-left: 25px;
        font-size: 25px;
    }

    .btn-delete-station {
        font-size: 25px;
        margin-left: 25px;
        margin-top: 45px;
    }
}